import {createContext , useContext , useReducer , useState, useEffect } from "react";
import axios from "axios"
export const  dataContext = createContext();

export const DataProvider = ({children})=>{
  const [data,setData] = useState([])
  useEffect(()=>{
    (async function(){
      try{
      const {data} = await axios.get("https://youtube.amitthakur3.repl.co/database");
      setData(data)
      }catch(error){
        console.log(error)
      }
    })()
  },[])

  const [ state , dispatch] = useReducer(( state , {type,payload} )=>{
    switch(type){
      case "LIKE":
        const likeVideo = data.find((item)=>item.id === parseInt(payload,10))
        const checkVideo = state.like.find((item)=> item.id === parseInt(payload,10))
        const unLikeVideo = state.like.filter((video)=>video.id !== payload)
        if(!checkVideo){
          return {...state , like : [...state.like ,likeVideo]}
        }
          else{
            return {...state , like : unLikeVideo}
        }
      default : 
      return state;

      case "DISLIKE":
        const disLikeVideo = state.like.filter((video)=>video.id !== payload)
        return {...state , like : disLikeVideo}

      case "WATCH LATER" : 
      const watchLaterVideo = data.find((item)=>item.id === parseInt(payload,10))
      const checkWatchVideo = state.watchLater.find((item)=> item.id === parseInt(payload,10))
      const unWatchVideo = state.watchLater.filter((video)=>video.id !== payload)
      if(!checkWatchVideo){
        return {...state , watchLater : [...state.watchLater ,watchLaterVideo]}
      }
        else{
          return {...state , watchLater : unWatchVideo}
      }
      case "HISTORY":
        const historyData = data.find((video)=>video.id === parseInt(payload,10));
        const checkHistory = state.history.find((video)=> video.id === historyData.id);
        const filterHistory = state.history.filter((video)=>video.id !== parseInt(payload,10));
        if(!checkHistory){
          return {...state, history : [historyData , ...state.history]}
        }else{
          const newHistory = {...state, history :  filterHistory};
          return {...state,history : [historyData, ...newHistory.history]}
        }
         
      case "CATEGORY" :
        
      return {...state, category : payload };

    }
  },{like :[] , watchLater : [], history : [] , category : "ALL"})
  function  getCategorizedData(videoData , category){
    if( category === "ALL"){
      return videoData
    }
    else{
    const filterdata = videoData.filter((video)=>video.category === category)
    return filterdata
    }
  }
  const CategorizedData = getCategorizedData(data , state.category)
  return(
    <dataContext.Provider value = {{data ,state, dispatch , CategorizedData}}>
      {children}
    </dataContext.Provider>
  )
}

export const useData = ()=>{
  return useContext(dataContext)
}
