import {Link} from "react-router-dom";
import "./component_css.js/section.css";
import {useData} from "../context/dataContext"
import {Categories} from "./categories"
export const Home = () => {
  const {CategorizedData ,dispatch} = useData()
  return (
    <div className = "section">
      
      <div className="content">
      <div className = "fixed-slide">
      <Categories />
      </div>
      {CategorizedData.map(({ id ,thumbnail ,title , channel , profile_img }) => {
        return (
          
            <div className = "video" key={id}>
            <Link className = "link" 
              onClick = {()=>dispatch({type : "HISTORY", payload : id , method : "ADD"})}
              to= {`/watch/${id}`}>
            <img   src={thumbnail} width ="360px"  alt  = "no pic"/>
            
            <div className = "video-footer" key = {id}>
              <div className  = "profile-pic"><img src = {profile_img} alt ="error"/></div>
              <div className = "video-detail">
                <div className = "title"><b>{title}</b></div>
                <div className = "channel">{channel}</div>
              </div>
            </div>
            </Link>
          </div>
          
        );
      })}
</div>

    
    </div>
  );
};






















