
import {Link} from "react-router-dom";
import ThreeDot from "../svg/three-dot.svg"
import {useData} from "../context/dataContext"
import "./component_css.js/watchLater.css"
export const WatchLater = ()=>{
  const {state} = useData()
  return(
    <div className = "watch-later-container">
      <h1>Watch Later </h1>
      <div>Total videos : {state.watchLater.length}</div>
      {
        state.watchLater.map(({id,thumbnail ,title,channel})=>{
          return(
            <div key = {id} className = "watch-later-video">
              <Link  to = {`/watch/${id}`} ><div className = "watch-later-video-thumbnail"><img  src = {thumbnail} alt = "processing"/></div></Link>
              <div className = "watch-later-video-details">
                <div className = "title">{title}</div>
                <div className  = "channel">{channel}</div>
              </div>
              <div className = "three-dot-menu">
                <img src = {ThreeDot} alt = "proceesing"/>
              </div>
            
            </div>
          )
        })
        
      }
    </div>
  )
}