import "./component_css.js/liked-videos.css";
import {Link} from "react-router-dom";
import ThreeDot from "../svg/three-dot.svg"
import {useData} from "../context/dataContext"
export const LikedVideos = ()=>{
  const {state} = useData()
  return (
    <div className = "liked-videos-container">
      <h1>Liked videos</h1>
      <div>Total videos : {state.like.length}</div>
      {
        state.like.map(({id,thumbnail ,title,channel})=>{
          return(
            <div className = "liked-video">
              <Link  className = "link" to = {`/watch/${id}`} ><div className = "liked-video-thumbnail"><img  src = {thumbnail} alt = "processing"/></div></Link>
              <div className = "liked-video-details">
                <div className = "title">{title}</div>
                <div className  = "channel">{channel}</div>
              </div>
              <div className = "three-dot-menu">
                <img src = {ThreeDot} alt = "proceesing"/>
              </div>
            
            </div>
          )
        })
        
      }
    </div>
  )
}

