
import {useData} from "../context/dataContext"
import {Videotemplate} from "./videoTemplate";
export const History = ()=>{
  const {state} = useData();
  return(
    <div className = "videos-container">
       {
        state.history.map(({id,thumbnail ,title,channel})=>{
          return (
          <div key ={id}>
            <Videotemplate  id = {id} thumbnail = {thumbnail}  title =  {title}  channel = {channel} />
          </div>)
        })
      }
      </div>
  )
    }