import {useState} from "react"
import { useParams } from "react-router";
import "./component_css.js/video-container.css";
import {useData} from "../context/dataContext"
export const VideoContainer = ()=>{
  const {id} = useParams();
  const {data , dispatch}= useData();
  const filterVideo = data.filter((item)=>item.id === parseInt(id,10))
  const [detail , setDetail] = useState(false)
  return(
    <div className = "watch-video">
      {
        filterVideo.map(({id,title, url ,profile_img , channel})=>{
          return(
          <div key = {id}>
            <iframe   src={url} title="YouTube video player" frameBorder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowFullScreen></iframe>
            <div  onClick = {()=>{setDetail(!detail)}} className = "video-title"><b>{title}</b></div>
            <button onClick = {()=>dispatch({type : "LIKE" , payload : id})}>like</button>
            <button onClick = {()=>dispatch({type : "DISLIKE" , payload : id})}>dislike</button>
            <button onClick = {()=>dispatch({type : "WATCH LATER" , payload : id})} >watch later</button>
            <button>playlist</button>
            <div className = "video-footer" key = {id}>
              <div className  = "profile-pic"><img src = {profile_img} alt ="error"/></div>
              <div className = "video-detail">
                <div className = "title"><b>{channel}</b></div>
                <div className = "channel">123k Subs</div>
              </div>
              
            </div>
            {detail && <div>Description</div>}
          </div>
          
            
          )
        })
      }
    </div>
  )
}